<template>
  <div class="create">
    <Panel>
      <template #header>
        <router-link to="/">主页</router-link>
        <span>/</span>
        <span v-if="$route.path==='/topic/create'">发布话题</span>
        <span v-else>编辑话题</span>
      </template>
      <template #content>
        <el-form ref="form" :model="form" :rules="rules">
          <el-form-item label="板块：" label-width="80px" prop="tab">
            <el-select v-model="form.tab" clearable placeholder="请选择">
              <el-option label="分享" value="share"></el-option>
              <el-option label="问答" value="ask"></el-option>
              <el-option label="招聘" value="job"></el-option>
              <el-option label="客户端测试" value="dev"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="标题：" label-width="80px" prop="title">
            <el-input v-model="form.title"></el-input>
          </el-form-item>
          <el-form-item>
            <mavon-editor
              v-model="form.content"
              defaultOpen="edit"
              :subfield="false"
              :boxShadow="false"
            />
          </el-form-item>
          <el-form-item>
            <el-button v-if="$route.path==='/topic/create'" type="primary" @click="create">立即创建</el-button>
            <el-button v-else type="primary" @click="save">保存</el-button>
          </el-form-item>
        </el-form>
      </template>
    </Panel>
    <Aside>
      <template #header>Markdown 语法参考</template>
      <template #content>
        <p>### 单行的标题</p>
        <p>**粗体**</p>
        <p>`console.log('行内代码')`</p>
        <p>```js\n code \n``` 标记代码块</p>
        <p>[内容](链接)</p>
        <p>![文字说明](图片链接)</p>
      </template>
    </Aside>
  </div>
</template>

<script>
import { mapActions,mapState } from "vuex";
import Panel from "../containers/Panel.vue";
import Aside from "../containers/Aside.vue";
import Turndown from 'turndown'
export default {
  components: {
    Panel,
    Aside
  },
  computed: {
    ...mapState({
      newPostId:state=>state.post.newPostId
    })

  },
  data() {
    return {
      form: {
        title: "",
        tab: "",
        content: ""
      },
      newPostId:null,
      rules: {
        title: [
          {
            required: true,
            message: "请输入标题",
            trigger: "blur"
          },
          {
            min: 6,
            message: "标题字数六字以上",
            trigger: "blur"
          }
        ],
        tab: [
          {
            required: true,
            message: "请选择板块",
            trigger: "change"
          }
        ]
      }
    };
  },
  computed: {
    id() {
      return this.$route.params.id;
    },
    ...mapState({
      post: state => state.post.post,
    }),
    token(){
      return localStorage.getItem('token')
    }
  },
  created () {
    // 编辑 获取post
    if(this.id){
    this.getPost(this.id);
    console.log(this.post)
    const {title,tab,content} = this.post
     var turndownService = new Turndown()
    const newContent = turndownService.turndown( content.replace('<div class="markdown-text">','').replace('</div>',''))
    this.form = {title,tab,content:newContent}
    };
  },
  methods: {
    ...mapActions(["createPost",'getPost','updatePost']),
    // 创建
    create() {
      this.$refs.form.validate(async bool => {
        if (bool) {
          // const token = localStorage.getItem('token')
          const {title,tab,content} = this.form
          const res =  await this.$axios.post(`/topics`,{accesstoken: this.token,title,tab,content})
          this.newPostId = res.topic_id

          this.$router.push(`/post/${this.newPostId}`)
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    // 编辑保存
    async save(){
      const {title,tab,content} = this.form
      this.$axios.post(`/topics/update`,{accesstoken: this.token,topic_id:this.id,title,tab,content})
      this.$router.push(`/post/${this.id}`)
    }
  }
};
</script>

<style lang="less">
.create {
  display: flex;
}
</style>